import { Employee } from "generated/prisma";
import { CreateDepartmentDto } from "../types/dtos/department.dto";
import { ExpandedEmployee } from "../types/prisma.types";

export type DepartmentForm = {
  name: string;
  description: string;
  employees: (Employee | ExpandedEmployee)[];
};

export const getDefaultDepartmentForm = (): DepartmentForm => ({
  name: '',
  description: '',
  employees: [] as (Employee | ExpandedEmployee)[]
});

export const convertDepartmentFormToDto = (form: DepartmentForm): CreateDepartmentDto => ({
  name: form.name.trim(),
  description: form.description.trim() || null,
  employeeIds: form.employees.map(e => e.id)
});